* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  list-style: none;
  margin: 0px;
}

.country-list {
  padding: 0px;
  margin: 0px;
}

.country-info__properties {
  font-weight: bold;
}

.country-info__container {
  display: flex;
}

.country-info__title {
  margin: 0px;
  margin-left: 10px;
}
